
Template.__checkName("settings");
Template["settings"] = new Template("Template.settings", (function() {
  var view = this;
  return [ HTML.Raw("<head>\n\n</head>\n\n  "), HTML.BODY("\n        ", Blaze._TemplateWith(function() {
    return {
      collection: Spacebars.call("Collection"),
      type: Spacebars.call(view.lookup("action")),
      doc: Spacebars.call(view.lookup("editDoc")),
      id: Spacebars.call("collectionId"),
      autosave: Spacebars.call(true)
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("autoForm"), function() {
      return [ "\n          ", HTML.FIELDSET("\n            ", Blaze._TemplateWith(function() {
        return {
          name: Spacebars.call("slider")
        };
      }, function() {
        return Spacebars.include(view.lookupTemplate("afQuickField"));
      }), "\n          "), "\n          ", HTML.BUTTON({
        type: "submit",
        "class": "btn btn-primary"
      }, Blaze.View("lookup:action", function() {
        return Spacebars.mustache(view.lookup("action"));
      })), "\n        " ];
    });
  }), "\n  "), "\n\n  ", HTML.SCRIPT("\n  \n  ") ];
}));
